import {Injectable} from '@angular/core';
import {Http,Headers} from '@angular/http';
import 'rxjs/add/operator/map';
import {Exprience} from '../Models/Expriences';
import {globalVariables} from '../globalVariables';


@Injectable()
export class ExprienceServices{
    exp :Exprience={};
    constructor(private http:Http){}

    // Get all experiences
    getAllExp(){
        let header = new Headers();
        return this.http.get(globalVariables.API_ENDPOINT+'exprience/get_all')  
            .map(res=>res.json());     
           
    }

    // Add new experience
    addExp(exp){
        let header = new Headers();
        header.append('Content-Type','application/json');
        return this.http.post(globalVariables.API_ENDPOINT+'exprience/add',exp,{headers:header})
        .map(res=>res);
    }

    // Edit existed experience 
    editExp(exp,id:String){
        let header = new Headers();
        header.append('Content-Type','application/json');
        return this.http.put(globalVariables.API_ENDPOINT+'exprience/update/'+id,exp,{headers:header})
        .map(res=>res);
    }

    //Delete existed experience
    deleteExp(id:String){
        let header=new Headers();
        header.append('Content-Type','application/json');
        return this.http.delete(globalVariables.API_ENDPOINT+'exprience/remove/'+id,{headers:header})
        .map(res=>res);
        
    }



}


