@NgModule({
  declarations: [
    AppComponent,
    SkillsComponent,
    EducationComponent,
    ExprienceComponent,
    AboutComponent,
    BgcolordirectiveDirective,
    DashboardComponent,
    MainpageComponent ,

  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    HttpClientModule,
    AppRoutingModule
  ],
  providers: [SkillServices,UserServices,ExprienceServices, EducationServices,AppServices],
  bootstrap: [AppComponent]
})
export class AppModule { }



