import { Component, OnInit, AfterViewInit } from '@angular/core';
import { ExprienceServices } from './exprience.service';
import { Exprience } from '../Models/Expriences';

@Component({
  selector: 'app-exprience',
  templateUrl: './exprience.component.html',
  styleUrls: ['./exprience.component.css']
})
export class ExprienceComponent implements OnInit {

  constructor(private exprienceServices: ExprienceServices) { }

  experiences: Exprience[] = [];
  ngOnInit() {
    this.exprienceServices.getAllExp().subscribe(data => {
      this.experiences = data.expriences;
    
    });
  }

}



