import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import 'rxjs/add/operator/map';
import { Education } from '../Models/Educations';
import {globalVariables} from '../globalVariables';

@Injectable()
export class EducationServices {

    edu: Education = {};
    constructor(private http: Http) { }

    // Get all education
    getAllExp() {
        let header = new Headers();
        return this.http.get(globalVariables.API_ENDPOINT+'education/get_all')
            .map(res => res.json());
    }

    // Add new education
    addExp(edu) {
        let header = new Headers();
        header.append('Content-Type', 'application/json');
        return this.http.post(globalVariables.API_ENDPOINT+'education/add', edu, { headers: header })
            .map(res => res.json());
    }

    // Edit existed education 
    editExp(edu, id: String) {
        let header = new Headers();
        header.append('Content-Type', 'application/json');
        return this.http.put(globalVariables.API_ENDPOINT+'education/update/' + id, edu, { headers: header })
            .map(res => res.json());
    }
}


