import { Component, OnInit, Input, ViewChild, AfterViewInit, AfterContentInit, AfterContentChecked, AfterViewChecked } from '@angular/core';
import {AppServices} from '../app.services';
import { Router } from '@angular/router';
import {Skill} from '../Models/Skills';
import {Exprience} from '../Models/Expriences';
import {SkillServices} from '../skills/skills.service';
import {ExprienceServices} from '../exprience/exprience.service';
//import { ConsoleReporter } from 'jasmine';


declare var jquery: any;
declare var $: any;
@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit{
  
  dbSkill:any;
  dbExperience:any;
  new_Exp:Exprience={};  
  new_skill:Skill={};
  skill:Skill={};
  experience:Exprience={};


  constructor(private appServices:AppServices,
    private router:Router,
    private skillServices:SkillServices,
    private experienceServices:ExprienceServices) { }

  ngOnInit() {
    this.appServices.authenticateUser().subscribe(res=>{
     
      if(res.status!=true){
        this.router.navigateByUrl('/home');
      }
      else{
        //this.dbSkill=this.skillServices.loadSkillsData();
        this.skillServices.getAllSkill().subscribe(res=>{
          this.dbSkill=res.skills;         
       });  
        
        this.experienceServices.getAllExp().subscribe(res=>{
          this.dbExperience=res.expriences;
        })
        
      }
    });
  }


  updateSkill(_id,item){
    this.skill.Title=item.Title;
    this.skill.Time=item.Time;
    this.skill.BgColor=item.BgColor;
    this.skill.Description=item.Description;
    this.skill.IconUrl=item.IconUrl;
   
    this.skillServices.updateSkill(_id,this.skill).subscribe(res=>{
      alert(res.text());
    });
  }

  deleteSkill(_id,title){
    var r = confirm("Delete ' "+title+"' skill ?" );
    if (r == true) {
       this.skillServices.deleteSkill(_id).subscribe(res=>{
         this.ngOnInit();
         $('.modal').remove();

       });
    } else {
       
    }
  }

  addSkill(new_skill){
    
    this.skillServices.addSkill(new_skill).subscribe(res=>{
    
      this.ngOnInit();
      $('.modal-backdrop').remove();
      $('.modal').remove();
    })
  }

  addExp(new_Exp){
    this.experienceServices.addExp(new_Exp).subscribe(res=>{
      alert(res.text());
      $('.modal-backdrop').remove();
      $('.modal').remove();
      this.experienceServices.getAllExp().subscribe(reload=>{
        this.dbExperience=reload.expriences;
      });
     
    });
  }

  updateExp(_id,item){
    this.experience.CompanyName=item.CompanyName;
    this.experience.Description=item.Description;
    this.experience.StartWhen=item.StartWhen;
    this.experience.EndWhen=item.EndWhen;
    this.experience.IconUrl=item.IconUrl;
    this.experienceServices.editExp(this.experience,_id).subscribe(res=>{
      alert(res.text());
    });
  }

  deleteExp(_id,CompanyName){
    this.experienceServices.deleteExp(_id).subscribe(res=>{
      alert(res.text());
      this.experienceServices.getAllExp().subscribe(reload=>{
        this.dbExperience=reload.expriences;
      });
    });
  }

}



