import { Component, OnInit } from '@angular/core';
import {UserServices} from './about.service';
import {User} from '../Models/Users';


declare var jquery: any;
declare var $: any;
@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.css']
})
export class AboutComponent implements OnInit {

  hoby:boolean=false;
  contact:boolean=false;
  public user:User={};

  constructor(private userService:UserServices) { }

  ngOnInit() {
    this.getUserInfor();
  }

  public getUserInfor(){
    this.userService.getUserInfor().subscribe(data=>{     
      this.user.Address=data.Address;
      this.user.DateOfBirth=data.DateOfBirth;
      this.user.Description=data.Description;
      this.user.FirstName=data.FirstName;
      this.user.LastName=data.LastName;
      this.user.FullName=data.FullName;
      this.user.Hobbies=data.Hobbies;
      this.user.Email=data.Email;    
      this.user.FbLink=data.Social.fbUrl;
      this.user.GmailLink=data.Social.gmail;
      this.user.GitHubLink=data.Social.github;
     
    });    
  }

  hover_panel(){
    $("#panel").slideDown("slow");
    
  }

  mouseleave_panel(){
    $("#panel").slideUp("slow");
  }

  contact_click(){
    this.contact=true;
    this.hoby=false;
    $("#contact_line").slideDown("slow");
    
  }

  hoby_click(){
    this.contact=false;
    this.hoby=true;
    $("#hobbies_line").slideDown("slow");
    
  }

}



