import { Directive,OnInit, ElementRef } from '@angular/core';

@Directive({
  selector: '[appBgcolordirective]'
})
export class BgcolordirectiveDirective implements OnInit{

  constructor(private el: ElementRef) { }

  colors:string[]=["#57E964","crimson","#B93B8F","#157DEC"];

  ngOnInit(){
    this.backgroundset(this.colors[Math.floor(Math.random()*this.colors.length)]);
  }
  private backgroundset(color:string){
      this.el.nativeElement.style.BackgroundColor=color;
  }

}



