import { Component, OnInit } from '@angular/core';
import { SkillServices } from '../skills/skills.service';
import { AppServices } from '../app.services';
import { User } from '../Models/Users';
import { Router } from '@angular/router';


declare var jquery: any;
declare var $: any;
@Component({
  selector: 'app-mainpage',
  templateUrl: './mainpage.component.html',
  styleUrls: ['./mainpage.component.css']
})
export class MainpageComponent implements OnInit {

  constructor(private router: Router, private skills: SkillServices, private appService: AppServices) { }

  ngOnInit() {
    // $('#education').hide();
    this.about();
  }

  user: User = {};
  Email: String = "";
  Password: String = "";

  login() {

    this.appService.logIn(this.user).subscribe(data => {
     
      this.appService.storeUserData(data.token, data.user);
      this.router.navigateByUrl('/dashboard');
      $('.modal-backdrop').remove();
    });
  }

  education() {
    $('html, body').animate({
      scrollTop: $("#education").offset().top - 90
    }, 1000);
  }

  skill() {
    $('html, body').animate({
      scrollTop: $("#skills").offset().top - 90
    }, 1000);
  }

  exprience() {
    $('html, body').animate({
      scrollTop: $("#exprience").offset().top - 90
    }, 1000);
  }

  about() {
    $('html, body').animate({
      scrollTop: $("#about").offset().top - 90
    }, 1000);
  }
}



