import { Component, OnInit ,ElementRef} from '@angular/core';
import {SkillServices} from './skills.service';
import {BgcolordirectiveDirective} from './bgcolordirective.directive';
import {Skill} from '../Models/Skills';
@Component({
  selector: 'app-skills',
  templateUrl: './skills.component.html',
  styleUrls: ['./skills.component.css']
})
export class SkillsComponent implements OnInit {

  constructor(private skillServices:SkillServices,private el:ElementRef) { }
  skills:Skill[]=[];
  ngOnInit() {
    this.getAllSkill();
  }


  getSkills(){
    this.getAllSkill();
   // this.setBgColor();
  }

  getAllSkill(){
   this.skillServices.getAllSkill().subscribe(data=>{   
     this.skills=data.skills;
     this.skillServices.storeSkillData(this.skills);
    
   }); 
  }

  setBgColor():string{
    for(var i=0;i<=Object.keys(this.skills).length;i++){
      if(this.skills[i].Title=="Mobile Develop"){
        return "yellow";
      }

      else if(this.skills[i].Title==".NET Core"){
        return "pink";
      }
 
    }
  }

}



