import { Injectable } from '@angular/core';
import {Http, Headers} from '@angular/http';
import 'rxjs/add/operator/map';
import {Skill} from '../Models/Skills';
import {globalVariables} from '../globalVariables';


@Injectable()
export class SkillServices{
    constructor(private http:Http){}
    skill:any;
    getAllSkill(){
        let header=new Headers();
        header.append('Content-Type','application/json');
        return this.http.get(globalVariables.API_ENDPOINT+'skill/get_all')
                     .map(res=>res.json());
    }

    storeSkillData(skills) {
        localStorage.setItem('skills', JSON.stringify(skills));
        this.skill = skills;
        
      }

    loadSkillsData():any{
        const skills= JSON.parse(localStorage.getItem('skills'));
      
        return skills;
    }

    updateSkill(_id,skill_updated){
        let header= new Headers();
        header.append('Content-Type','application/json');
        return this.http.put(globalVariables.API_ENDPOINT+'skill/update/'+_id,skill_updated,{ headers: header })
            .map(res=>res)
    }

    deleteSkill(_id){
        let header= new Headers();
        header.append('Content-Type','application/json');
        return this.http.delete(globalVariables.API_ENDPOINT+'skill/delete/'+_id,{ headers: header })
            .map(res=>res)
    }

    addSkill(new_skill){
        let header= new Headers();
        header.append('Content-Type','application/json');
        return this.http.post(globalVariables.API_ENDPOINT+'skill/add/',new_skill,{ headers: header })
            .map(res=>res)
    }

}


